import Style from './va-datepicker.styl';
import flatpickr from 'flatpickr';
import { config } from './va-datepicker.config';
import { key as LocaleKey } from 'flatpickr/dist/types/locale';
import { BaseOptions } from 'flatpickr/dist/types/options';
import { LitElement, TemplateResult, html, customElement, css, unsafeCSS, CSSResult } from 'lit-element';

/*
* We import all the locales we use as part of the js for simplicity (no file copy, no path changes, etc.)
* English is default language built-in without locale file
* If we want to improve bundle size, we could also only import 1 locale related to the current page with html script tag
* However each locale file is ~1.3k (gzipped ~750) so it's not a significant optimization
*/
import 'flatpickr/dist/l10n/de.js';
import 'flatpickr/dist/l10n/it.js';
import 'flatpickr/dist/l10n/fr.js';

/**
 * The tagname of the current component
 */
const COMPONENT_NAME = 'va-datepicker';

const testYLE = {
    myFunc() {
        return 29;
    },
};

(window as any).test = testYLE;

/**
 * A custom datepicker webcomponent which embeed the flatpickr lib
 * to manage the datepicker part
 * @see https://flatpickr.js.org
 *
 * !! Even if possible, do not access the inner input from outside of
 * the component directly, but use the webcomponent provided accessors
 */
@customElement(COMPONENT_NAME)
export class VaDatepicker extends LitElement {
    /**
     * The flatpickr instance created by the webcomp instance
     */
    private pickerInstance: flatpickr.Instance;

    /**
     * The Vaudoise configuration for the datepickr
     */
    private config: Partial<BaseOptions> = config;

    /**
     * The date field
     * @returns the date input element from the template
     */
    private get dateInput(): HTMLInputElement {
        return this.firstElementChild as HTMLInputElement;
    }

    /**
     * The datepicker can be configured to select a range
     * This accessor will return all selected values
     * @returns the array of selected dates
     */
    get selectedValues(): Array<Date> {
        return this.pickerInstance.selectedDates;
    }

    /**
     * Gets the selected value with the format chosen for code logic
     * @returns the selected date
     */
    get value(): string {
        const selectedValues = this.selectedValues;
        if (!selectedValues.length) {
            return '';
        }
        return this.pickerInstance.formatDate(selectedValues[0], config.dateFormat);
    }

    /**
     * Sets the selected value with the format chosen for code logic
     * @param val - the date to select
     */
    set value(val: string) {
        this.pickerInstance.setDate(val, true, config.dateFormat);
    }

    /**
     * Gets the selected value formatted like displayed to the user
     * @returns the selected date
     */
    get formattedValue(): string {
        const selectedValues = this.selectedValues;
        if (!selectedValues.length) {
            return '';
        }
        return this.pickerInstance.formatDate(selectedValues[0], config.altFormat);
    }

    /**
     * Create a Datepicker instance
     */
    constructor() {
        super();
        if (!this.id) {
            this.id = Math.random().toString().substr(2);
        }

        // fetch language value from html[lang] attribute
        this.config = {
            ...config,
            locale: flatpickr.l10ns[this.getDatePickerLocaleFromPage()],
        };
    }

    /**
     * Gets the locale define at html[lang] level and return a flatpickr valid locale
     * @returns the locale to be used by the datepicker
     */
    getDatePickerLocaleFromPage(): LocaleKey {
        let localeName: LocaleKey = 'default';
        const lang = document.documentElement.lang.substr(0, 2) as LocaleKey;

        if (flatpickr.l10ns[lang]) {
            localeName = lang;
        }
        return localeName;
    }

    /**
     * @override
     */
    firstUpdated(): void {
        this.pickerInstance = flatpickr(this.dateInput, this.config);
    }

    /**
     * This prevent the use of the shadowDom by returning
     * directly the instance instead of a shadow root
     * @returns the current instance of the class
     */
    createRenderRoot(): VaDatepicker {
        return this;
    }

    /**
     * @override
     */
    render(): TemplateResult {
        return html`
            <input type="text" id="dp-${this.id}" class="va-datepicker-input form-control" />
            <style class="d-none">${VaDatepicker.styles}</style>
        `;
    }

    /**
     * Css styles
     * @returns the CSS styles
     */
    static get styles(): Array<CSSResult> {
        return [css`${unsafeCSS(Style)}`];
    }
}
