import { BaseOptions } from 'flatpickr/dist/types/options';

/**
 * Our default Flatpickr config
 * @see https://flatpickr.js.org/options/
 */
export const config: Partial<BaseOptions> = {
    /**
     * Allows the user to enter a date directly into the input field. By default, direct entry is disabled.
     */
    allowInput: true,
    /**
     * Show the user a readable date (as per altFormat), but return something totally different to the server.
     */
    altInput: true,
    /**
     * The format used to be displayed to the user
     */
    altFormat: 'd.m.Y',
    /**
     * The format used to be used programmatically (ex: server request)
     */
    dateFormat: 'Y-m-d',
};
